.BGImageContainerH {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    width: 100%;
    box-sizing: border-box;
}


.HeaderContainer {
    display: flex;
    align-items: center; 
    justify-content: space-between; 
    width: 100%;  
    margin-top: 2%;
    gap: min(60px, 90%);
}

.NameContainer {
    color: rgb(170, 170, 170);  
    font-size: 36px;  
    padding-left: max(8%,0%);  
    display: flex;
    align-items: center; 
    width: auto; 
    white-space: nowrap; 
}

.menu {
    background-color: transparent;  
    display: flex;  
    justify-content: flex-end;  
    margin-right: 2%;
    flex-shrink: 0;
}

.menu ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: flex-end;
    margin-right: 3%;
    background-color: transparent;
}

.menu li {
    margin-left: 6%;
    background-color: transparent;
    transition: background-color 0.3s ease, color 0.3s ease;
    flex-shrink: 0;
}
.menu a {
    color: rgb(170, 170, 170);
    text-decoration: none;
}

.menu a:hover {
    color: rgb(137, 77, 0);  
    
}


.FirstContainerH {
    background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.7));
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%; 
    height: calc(100vh - 4%);  
    color: white;
}



.LicContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 92%;
    background-color: rgb(9, 9, 9);
}


h1 {
    font-size: 48px;
    margin-bottom: 10px;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5); 
    color: white;
    font-family: 'Rajdhani', sans-serif;
    text-align: center;

}

p {
    font-size: 24px;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5); 
    color: white;
    font-weight: 200;
    
}

h2 {
    font-size: 12px;
    color: rgb(65, 65, 65);
    text-align: center;

}



