    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;  
}

 .QualityControlPanelContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 20px; 
    padding: 60px; 
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.5), 0 6px 20px 0 rgba(0, 0, 0, 0.1);
    background: rgba(30, 30, 30, 0.6); 
}

.Info_PhotoContainer {
    border: 2px dashed #747474; /*Discontinuous borders */
    padding: 10px 80px; 
    box-sizing: border-box;
}

.PredictionContainer,
.ImageContainer {
    min-height: 150px;
}

.placeholder {
    color: #ccc;
