}

.ButtonsContainer {
    display: flex;
    flex-direction: row; 
    justify-content: center;
    gap: 40px; 
}


.image-container img {
    max-width: 100%;
    max-height: 100%; 
}




.black-button {
    background-color: #000000;
    color: #FFFFFF;
